.archive-table {
    border-collapse: collapse !important;
    margin: 0;
    width: 100%;
    padding: 0;
    max-width: 430px;
    margin-top: 15px;
    font-size: 14px;
    line-height: 22px;

    & td {
        width: 14.2%;
        min-width: 64px;
        height: 40px;
        padding: 0;
        border-left: 1px solid #d7d7d7 !important;
        border-bottom: 1px solid #d7d7d7 !important;
        border: 0;
        text-align: center;

        @media(max-width: 500px) {
            height: 35px;
            min-width: initial;
        }

        &:first-child {
            border-left: none !important;
        }

        a {
            display: inline-block;
            font-weight: 400;
            text-align: center;
            line-height: 30px;
            color: #666;
            width: 100%;
            height: 100%;
            filter: dropshadow(color=#fff, offx=1, offy=1);
            font-size: 14px;
            font-family: "OpenSans", sans-serif;
            padding: .2em;
            // pointer-events: none;

            @media(max-width: 500px) {
                line-height: 28px;
            }
        }
    }

    th {
        font-weight: 400;
        padding: 0;
        width: 14.2%;
        min-width: 64px;
        height: 40px;
        border-left: 1px solid #d7d7d7;
        border-bottom: 1px solid #d7d7d7;

        @media(max-width: 500px) {
            min-width: initial;
        }

        &:first-child {
            border-left: none !important;
        }
    }

    tr {
        padding: 0;
        margin: 0;
        border-bottom: none !important;
    }

    tbody {
        tr {
            &:last-child {
                td {
                    border-bottom: none !important;
                }
            }
        }
    }

    .active {
        background-color: $purple-3 !important;

        a {
            color: white !important;
        }
    }

    .selected {
        outline: 2px solid $purple-3 !important;
        outline-offset: -1px;
        transition: .2s ease-out;

        a {
            pointer-events: initial;
            font-weight: 900;
            color: #34558b;
        }

        &:hover {
            background-color: $purple-3;


            a {
                color: white;
            }
        }
    }
}