.news-basic {
    padding-bottom: 30px;
    border-bottom: 1px solid $gray-3;
    max-width: 946px;

    @media(max-width: 1024px) {
        max-width: 100%;
        padding-bottom: 24px;
    }

    @media(max-width: 640px) {
        padding-bottom: 20px;
    }

    &__cover {
        display: block;
        margin-top: 27px;

        &:hover .news-basic__title {
            color: $purple-1;
        }

        @media(max-width: 1235px) {
            margin-top: 8px;
        }
    }

    &__wrapper {
        display: flex;

        @media(max-width: 1235px) {
            align-items: center;
        }

        @media(max-width: 600px) {
            flex-direction: column;
        }

        &:hover .news-basic__img {
            &:after {
                opacity: 1;
            }
        }

        &:hover {
            .news-basic__title {
                color: $purple-3;
            }
        }
    }

    &__left {
        width: 542px;
        min-width: 542px;
        margin-right: 32px;


        @media(max-width: 1300px) {
            width: 490px;
            min-width: 490px;
            margin-right: 30px;
        }

        @media(max-width: 1235px) {
            width: 319px;
            min-width: 319px;
            margin-right: 20px;
        }

        @media(max-width: 1024px) {}

        @media(max-width: 768px) {
            width: 250px;
            min-width: 250px;
        }

        @media(max-width: 600px) {
            width: 100%;
            margin-right: 0;
        }

        &+.news-basic__right {
            padding: 37px 0px 0px;

            @media(max-width: 1235px) {
                padding-bottom: 0;
                padding-top: 0;
            }
        }
    }

    &__right {
        display: flex;
        flex-direction: column;
        width: 100%;
        // padding: 44px 0 60px;
        box-sizing: border-box;

        @media(max-width: 1235px) {
            width: auto;
            padding-bottom: 0;
            padding-top: 0;
        }

        @media(max-width: 600px) {
            width: 100%;
            margin-top: 22px;
        }
    }

    &__img {
        display: block;
        position: relative;
        height: 346px;
        background-position: center;
        background-size: cover;
        background-position-y: 0px;

        @media(max-width: 1235px) {
            height: 205px;
        }

        @media(max-width: 768px) {
            height: 220px;
        }

        @media(max-width: 600px) {
            height: 184px;
        }
    }

    &__text {
        margin-top: 10px;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 26px;
        letter-spacing: .5px;

        @media(max-width: 1024px) {
            font-size: 14px;
            line-height: 22px;
            margin-top: 0px;
        }

        @media(max-width: 600px) {
            margin-top: 3px;
        }
    }

    &__article {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 22px;
        color: $purple-3;

        @media(max-width: 1024px) {
            font-size: 12px;
            line-height: 20px;
        }
    }
}